$text_color: red;
//@mixin  box
//  display: block
//  width: 250px
//  height: 200px
//  background: red
@mixin box {
  display: block;
  width: 250px;
  height: 200px;
  background: red; }

h1 {
  color: $text_color;
  font-size: 50px; }

h2 {
  color: $text_color;
  font-size: 40px; }

.block {
  background-color: #000000;

  h3 {
    @include box; }

  &_color {
    background: aqua;

    button {
      @include box; } }

  .block_color {
    background-color: deeppink; } }
